* {
    padding: 0;
    margin: 0;
}

body {
    height: 100vh;
    width: 100%;
    background-image: 
    -webkit-linear-gradient(left,black, transparent , rgba(250, 250, 250, 0.3),  rgba(250, 250, 250, 0.6), rgba(250, 250, 250, 0.7), rgba(250, 250, 250, 0.6), rgba(250, 250, 250, 0.3),transparent,  black),
    url("/assets/witcher_ciri.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.cards_container { 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 680px;
    height: 100%;
    margin: 0 auto;
 }

 .inner-cards-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    max-height: 680px;
 }

.card {
    min-height: 120px;
    min-width: 120px;
    margin: 10px;
    border-radius: 2px;
    background-size: cover;
    background-position: center;
    border: 2px solid black;
    transition: .5s ease-in-out;

    &:hover {
        background-color: rgba(250, 250, 250, .9);
        background-color: white;
        transform: scale(1.05);
        border-color: #6b96db;
    }
}

.score-bar {
    width: 45%;
    margin: 20px;
    padding: 10px;
    margin-bottom: 30px;
    text-align: center;
    font-family: 'Lexend Zetta', sans-serif;
    font-size: 20px;
    letter-spacing: .2px;
    line-height: 30px;
    background-color: #6b96db;
    color: white;
    box-shadow: 0 0 8px 0 rgba(17, 22, 26, 0.16), 0 4px 8px 0 rgba(17, 22, 26, 0.08), 0 8px 16px 0 rgba(17, 22, 26, 0.08);
}

@media only screen and (max-width: 660px) {
    .card {
        min-height: 90px;
        min-width: 90px;
    }
    
    .inner-cards-container  {
        max-width: 490px;
        max-height: 450px;
    }

    .score-bar {
        padding: 10px;
        margin-bottom: 20px;
        font-size: 12px;
        line-height: 16px;
    }
}

@media only screen and (max-width: 480px) {
    .card {
        min-height: 60px;
        min-width: 60px;
    }
    
    .inner-cards-container  {
        max-width: 400px;
    }
}

@media only screen and (max-width: 390px) {
    .card {
        min-height: 50px;
        min-width: 50px;
    }
    
    .inner-cards-container  {
        max-width: 350px;
    }

    .score-bar {
        width: 50%;
        margin-bottom: 10px;
        font-size: 11px;
    }
}

.revers {
    background: rgba(250, 250, 250, .4) url('/assets/card_reverse/wolf_transparent_no_des.png') center / contain no-repeat;
}

.paired {
    visibility: hidden;
}

